        function deleteRisk() {
            var riskList = vm.risk.list;
            var len = riskList.length;
            for (var i = 0; i < len; i++) {
                if (riskList[i].selected) {
                    swal({
                        title: 'Are you sure?',
                        text: "You won't be able to revert deleting risk: " + riskList[i].id,
                        type: 'warning',
                        showCancelButton: true,
                        confirmButtonColor: '#3085d6',
                        cancelButtonColor: '#d33',
                        confirmButtonText: 'Yes, delete it!'
                    }).then(function() {
                        riskList.splice(i, 1);
                        vm.dtInstanceRisk.rerender();
                        updateIssueBarChart();
                        updateRiskRatingBarChart();
                        console.log(riskList);

                        swal(
                            'Deleted!',
                            'The risk has been deleted.',
                            'success'
                        );

                    })
                    return;
                }
            }
            swal('No item selected', 'Please select item', 'error');

        }

        function calculateCompetitorTotalScore(c_id) {

            var sum = 0;
            for (var f_id in vm.cpm.competitorRate) {
                sum += (vm.cpm.competitorRate[f_id][c_id] * vm.cpm.competitorScore[f_id][c_id]);
            }
            for (var i = 0; i < vm.cpm.competitors.length; i++) {
                if (vm.cpm.competitors[i].id == c_id) vm.cpm.competitors[i].totalScore = sum;
            }
        }

        function deleteFactorScoreRate(competitorID, factorID) {

            for (var f_id in vm.cpm.competitorRate) {
                if (f_id == factorID) {
                    delete vm.cpm.competitorRate[f_id];
                    delete vm.cpm.competitorScore[f_id];
                }
                if (competitorID > 0) {
                    for (var c_id in vm.cpm.competitorRate[f_id]) {
                        if (c_id == competitorID) {
                            delete vm.cpm.competitorRate[f_id][c_id];
                            delete vm.cpm.competitorScore[f_id][c_id];
                        }
                        //console.log(vm.cpm.competitorRate[f_id][c_id]);
                    }
                }


            }
            console.log(vm.cpm.competitorRate);
            console.log(vm.cpm.competitorScore);
        }

        function deleteCPMFactor() {
            var factors = vm.cpm.factors;
            var factorID = -1;
            for (var i = 0; i < factors.length; i++) {
                if (factors[i].selected) {
                    factorID = factors[i].id;
                    break;
                }
            }
            if (factorID == -1) {
                swal(
                    'No item selected',
                    'Please select item before using delete function',
                    'warning'
                )
                return;
            }
            swal({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Yes, delete it!'
            }).then(function() {

                var len = factors.length;
                for (var i = 0; i < len; i++) {
                    if (factors[i].id == factorID) {
                        factors.splice(i, 1);
                        $scope.$apply();
                        break;
                    }
                }
                vm.deleteFactorScoreRate(-1, factorID);
                swal(
                    'Deleted!',
                    'Factor has been deleted.',
                    'success'
                );
            })

        }

        function toggleFactor(factorIndex) {
            var factors = vm.cpm.factors;
            for (var i = 0; i < factors.length; i++) {
                factors[i].selected = false;

            }
            factors[factorIndex].selected = true;
            console.log("selected:" + factorIndex);
        }

        function deleteCompetitor(id) {
            var comp = vm.cpm.competitors;
            swal({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Yes, delete it!'
            }).then(function() {

                var len = comp.length;
                for (var i = 0; i < len; i++) {
                    if (comp[i].id == id) {
                        comp.splice(i, 1);
                        $scope.$apply();
                        break;
                    }
                }
                vm.deleteFactorScoreRate(id, -1);
                swal(
                    'Deleted!',
                    'Company has been deleted.',
                    'success'
                );
            })
        }

